<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="230"></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column prop="updateTime" label="">{{ this.today }}</el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="180">
        <template slot-scope="scope">
          <el-button type="text" size="small"
                     @click="$router.push(`/admin_users/edit/${scope.row._id}`)"></el-button>
          <!-- scope.row._id id -->
          <el-button type="text" size="small"
                     @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import moment from 'moment'
  import jwt from "jsonwebtoken";

  export default {
    name: "AdminUserList",
    data(){
      return {
        items: [],
        modal: [],
        id: '',
        today: ''
      }
    },
    methods:{
      async fetch() {
        const res = await this.$http.get('rest/admin_users')
        this.items = res.data
      },
      async remove(row) {
        this.$confirm(`"${row.name}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          await this.$http.delete(`rest/admin_users/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch() // 
        })
      },
      async lastLogin() {
        // 
        const res = await this.$http.get('rest/admin_users')
        this.model = res.data
        this.today = moment().format('LLL')
        this.id = jwt.verify(localStorage.token, 'errs55454da')
        // id
        this.model.forEach((item, index) => {
          if (item._id === this.id.id) {
            this.model[index].updateTime = this.today
            console.log(this.model[index].updateTime)
            this.$http.put(`rest/admin_users/${this.model[index]._id}`, this.model[index].updateTime) // 
          }
        })
      }
    },
    created() {
      this.lastLogin()
      this.fetch()
    }
  }
</script>

