<template>
  <div class="login-container">
    <el-card header="" class="login-card">
      <el-form @submit.native.prevent="login">

        <el-form-item label="">
          <el-input v-model="model.username"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="model.password" type="password"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>

      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      model: {},
      res: {
        data: {},
      }
    }
  },
  methods: {
    async login () {
      const res = await this.$http.post('login', this.model)
      localStorage.token = res.data.token  // token
      this.$router.push('/admin_users/list')  // 
      this.$message({
        type: 'success',
        message: ''
      })  // 
    },
  },
}
</script>

<style scoped>
  .login-card {
    width: 25rem;
    margin: 5rem auto;
  }
</style>
