<template>
  <el-container style="height: 100vh;">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-menu router
               unique-opened
               :default-active="$route.path"
               background-color="#545c64"
               text-color="#fff"
               active-text-color="#ffd04b">
        <!--        -->
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-menu"></i>
          </template>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/items/create"></el-menu-item>
            <el-menu-item index="/items/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/heroes/create"></el-menu-item>
            <el-menu-item index="/heroes/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/articles/create"></el-menu-item>
            <el-menu-item index="/articles/list"></el-menu-item>
          </el-menu-item-group>

        </el-submenu>

        <!--        -->
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-share"></i>
          </template>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/ads/create"></el-menu-item>
            <el-menu-item index="/ads/list"></el-menu-item>
          </el-menu-item-group>

        </el-submenu>

        <!--        -->
        <el-submenu index="3">

          <template slot="title">
            <i class="el-icon-s-help"></i>
          </template>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/categories/create"></el-menu-item>
            <el-menu-item index="/categories/list"></el-menu-item>
          </el-menu-item-group>

          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/admin_users/create"></el-menu-item>
            <el-menu-item index="/admin_users/list"></el-menu-item>
          </el-menu-item-group>

        </el-submenu>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style=" font-size: 12px">
        <el-menu :default-active="activeIndex"
                 class="el-menu-demo"
                 mode="horizontal"
                 background-color="#545c64"
                 text-color="#fff"
                 active-text-color="#ffd04b">

          <el-submenu index="2" class="items">
            <template slot="title"></template>
            <el-menu-item index="2-1"></el-menu-item>
            <el-menu-item index="2-2"></el-menu-item>
            <el-menu-item index="2-3"></el-menu-item>
          </el-submenu>

          <el-button type="text" @click="delUser" ></el-button>
          <el-menu-item index="1">
            <span>{{ this.user }}</span>
          </el-menu-item>

        </el-menu>
      </el-header>

      <el-main>
        <!--  -->
        <router-view :key="$route.path"></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  import jwt from 'jsonwebtoken'

  export default {
    data() {
      const item = {
        date: '2016-05-02',
        name: '',
        address: ' 1518 ',

      };
      return {
        tableData: Array(20).fill(item),
        activeIndex: '1',
        activeIndex2: '1',
        model: [
          {username: ''}
        ],
        user: '[]',
        id: ''
      }
    },
    methods: {
      queryIndex(id) {
        return id === this.model._id
      },

      async fetch() {
        // 
        const res = await this.$http.get('rest/admin_users')
        this.model = res.data
        // console.log(this.model)
        // tokenid
        this.id = jwt.verify(localStorage.token, 'errs55454da')
        // id
        for (const value of this.model) {
          if (value._id === this.id.id) {
            this.user = value.username
          }
        }
      },
      async delUser() {
        localStorage.token = ''
        this.$router.push('/login')
      }
    },
    created() {
      this.fetch()
    },
  };
</script>

<style scoped>
  .el-header {
    background-color: #545c64;
    color: #333;
    line-height: 60px;
  }

  .el-menu-demo {
    max-width: 1500px;
  }
  .el-menu-demo .items {
    float: right;
  }
  .el-aside {
    color: #333;
  }
</style>
